<template>
      <a-scene>
      <!-- <img src="../assets/images/ella_tamar_one.jpg" alt="" width="42" >     -->
      <a-entity position="0 102.25 -15"  particle-system="color: #be29ec, #d896ff" size="14" velocityValue="0 1 0"></a-entity>
        <a-assets>
            <img id="my-image" src="../assets/images/ella_tamar_one.jpg">
            <img id="ella_two" size="50" src="../assets/images/ella_tamar_two.jpeg">
            <img id="ella_ronnie" size="50" src="../assets/images/ella_ronnie.jpg">
        </a-assets>
        <a-image  position="0.5 1.5 -1" src="#my-image"></a-image>
        <a-image  position="-1 1.5 0" hight="50"  size="50" rotation="0 90 0" src="#ella_two"></a-image>
        <a-image  position="0.5 1.5 1" hight="50"  size="50" rotation="0 180 0" src="#ella_ronnie"></a-image>
        <a-text color="#efbbff" width="2" value="Ella,
        I love you so much. 
        You have given me 
        the greatest gift of all.
        A family. You are an incredible 
        mother, wife and friend.
        Thank you.
        Love Rony" position="1 1.5 -0.5" rotation="0 -90 0">
        </a-text>
      <a-box position="2 2.25 -15" rotation="0 45 0" color="#4CC3D9">
  <a-animation attribute="rotation"
               dur="10000"
               fill="forwards"
               to="0 360 0"
               repeat="indefinite"></a-animation>

      </a-box>

      <!-- <a-cylinder position="-2 2 -3" radius="0.5" height="1.5" color="#FFC65D"></a-cylinder> -->
      <!-- <a-plane position="0 0 0" rotation="-90 0 0" width="4" height="4" color="#be29ec"></a-plane> -->
      <a-sky color="black"></a-sky>
    </a-scene>
</template>
<script>
export default {
  data () {
    return {
      zs: this.getArr()
    }
  },
  methods: {
    getArr () {
      let arr = []
      for (let i = 5; i <= 100; i += 10) {
        arr.push(`${i} 1.25 -5`)
      }
      return arr
    }
  }
}
</script>





