import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Vr from '@/components/Vr'
import ThreeD from '@/components/ThreeD'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/vr',
      name: 'vr',
      component: Vr
    },
    {
      path: '/threed',
      name: 'threed',
      component: ThreeD
    }
  ]
})



