<template>
  <div>
    <img src="../assets/images/ella_tamar_one.jpg" alt="" width="42" >
    <h1>foo</h1>
    
    <div id="webgl"></div>
  </div>
</template>
<script>
import * as THREE from 'three'

export default {
  mounted () {
    const scene = this.scene()
    console.log(scene)
  },
  methods: {
    getBox (w, h, d) {
      const geometry = new THREE.BoxGeometry(w, d, h)
      const material = new THREE.MeshBasicMaterial({color: 0xff00})
      const mesh = new THREE.Mesh(geometry, material)
      return mesh
    },
    getPlane (size) {
      const geometry = new THREE.PlaneGeometry(size, size)
      const material = new THREE.MeshBasicMaterial({
        color: 'goldenrod',
        side: THREE.DoubleSide
      })
      const mesh = new THREE.Mesh(
        geometry,
        material
      )
      return mesh
    },
    scene () {
      /* eslint-disable no-new */
      const scene = new THREE.Scene()
      const camera = new THREE.PerspectiveCamera(
        45,
        window.innerWidth / window.innerHeight,
        1,
        1000
      )
      camera.position.z = 5
      camera.position.x = 1
      camera.position.y = 2
      camera.lookAt(new THREE.Vector3(0, 0, 0))
      const renderer = new THREE.WebGLRenderer()

      const box = this.getBox(1, 1, 1)
      const plane = this.getPlane(4)

      plane.name = 'plane-1'
      box.position.y = box.geometry.parameters.height / 2
      plane.rotation.x = Math.PI / 2
      // plane.position.y = 1
      scene.add(box)
      scene.add(plane)

      renderer.setSize(window.innerWidth, window.innerHeight)
      this.$el.querySelector('#webgl').appendChild(renderer.domElement)
      this.update(renderer, scene, camera)
      return scene
    },
    update (renderer, scene, camera) {
      renderer.render(scene, camera)

      // const plane = scene.getObjectByName('plane-1')
      // plane.rotation.y += 0.001
      // plane.rotation.x += 0.011

      // scene.traverse((child) => {
      //   child.scale.x += 0.001
      // })
      requestAnimationFrame(() => {
        this.update(renderer, scene, camera)
      })
    }
  }
}
</script>


